.coupon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  font-size: 12px !important;
  .coupon-content {
    max-width: 850px;
    width: 100%;
    .coupon-form {
      margin: 20px 0px;
      padding: 30px;
      padding-bottom: 50px;
      font-size: 12px !important;
      background: white;
      box-shadow: 0px 0px 5px #efefef;
      // max-width: 850px;
      // width: 100%;
      input {
        font-size: 12px;
      }
      .coupon-dropdown-container,
      .date-desc-container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
      }
      .coupon-discout-container {
        display: flex;
        flex-wrap: wrap;
        .coupon-discout-field {
          width: calc(33.33% - 10px);
          margin-right: 10px;
        }
        .coupon-discout-field:nth-child(2n + 3) {
          margin: 0px;
        }
      }
      .select-container {
        width: 100%;
        margin-right: 10px;
      }
      .select-container:last-child {
        margin-right: 0px;
      }

      .coupon-discout-field {
        width: 100%;
        margin-right: 10px;
        min-height: 70px;
      }
      .coupon-discout-field:last-child {
        margin-right: 0px;
      }
      .date-desc-container {
        .date-container {
          width: 100%;
        }
        .text-area-desc-container {
          width: 100%;
          padding: 0px 20px;
          .label {
            color: gray;
          }
          .text-area-desc {
            width: 100%;
            min-height: 50px;
            /* margin: 20px; */
            padding: 10px;
            border: 0px;
            border-bottom: 1px solid gray;
            height: 75px;
          }
        }
      }
      .button-container {
        button {
          width: auto;
          float: right;
          padding: 10px 40px;
        }
      }
    }
  }
}
